<template>
  <n-space style="margin: 10px">
        <n-button type="success" @click="showModalCard">曾总巡查工作次数+1</n-button>

	<n-popconfirm @positive-click="delLocalStorage">
      <template #trigger>
        <n-button type="error">我从没来过</n-button>
      </template>
      我曾的没有来过
    </n-popconfirm>
    <n-button type="info" @click="isTime = !isTime">自定义时间</n-button>
    <n-date-picker
      v-model:value="datetime"
      type="datetime"
      :disabled="disabled"
      :clearable="true"
      v-show="isTime"
    />
    <n-button type="info" @click="isModal = !isModal">自定义事件</n-button>
  </n-space>
  <n-scrollbar style="max-height: 720px">
    <n-grid :x-gap="12" :y-gap="8" :cols="4">
      <n-grid-item v-for="(item,index) in zengZongShuJu" :key="item.id">
        <n-card title="曾总到访">
		
          <template #header-extra>
		  <n-space>
		   <n-button size="tiny" type="success" @click="endtimeEdit(item.id,item.starttime,item.text, index)" :disabled = 'item.endtime ? true : false'>结束打卡</n-button>
            <n-popconfirm @positive-click="delItem(index)">
              <template #trigger>
                <n-button size="tiny" type="error">这次不算</n-button>
              </template>
              好的曾总
            </n-popconfirm>
			</n-space>
          </template>
		  <n-space vertical align='start'>
          <n-text>开始时间：{{ item.starttime }}</n-text>
        <n-text>内容： <n-text :type="item.text == '工作巡查' ? 'success' : 'error'"  >{{ item.text }} </n-text></n-text>
          <n-text>结束时间：{{ item.endtime }}</n-text>
        <n-text>巡查时长：{{item.timelength}} 分钟</n-text> 
    </n-space>
        
        </n-card>
      </n-grid-item>
    </n-grid>
  </n-scrollbar>

  <n-h3>
    曾总巡查工作次数共计：<n-text type="error">{{
      zengZongShuJu.length
    }}</n-text
    >次
  </n-h3>
{{serchText}}
 <n-modal
    v-model:show="showModal"
    class="custom-card"
    preset="card"
    title="打卡时间"
    size="huge"
	:mask-closable="false"
	style="width: 600px"
  >
 <n-space vertical>
    <n-text>打卡时间：{{nowTime}}</n-text>
	
	  <n-radio-group v-model:value="textValue" name="radiogroup">
    <n-space>
      <n-radio v-for="song in songs" :key="song.value" :value="song.value">
        {{ song.label }}
      </n-radio>
	  
    </n-space>
	
  </n-radio-group>
   <n-text>结束时间：{{endTime}}</n-text>
 
   </n-space>
    <template #footer>
     <n-space justify="end">
	 <n-button type='success' @click='enterData(0)'>打卡</n-button>
	 </n-space>
    </template>
  </n-modal>

</template>

<script setup>
import { reactive, ref } from "@vue/reactivity";
import dayjs from "dayjs";
import { useMessage } from "naive-ui";
const isTime = ref(false);
const showModal = ref(false);

const textValue = ref()
window.$message = useMessage();
const isModal = ref(false);
const serchText = ref();
const nowTime = ref()
const endTime = ref()
const duration = ref()
const operate = ref()
const itemIndex = ref()
const songs = ref([
        {
          value: "工作巡查",
          label: "工作巡查"
        },
        {
          value: '摸鱼巡查',
          label: '摸鱼巡查'
        }])
let zengZongShuJu = reactive(
  JSON.parse(localStorage.getItem("cishu"))
    ? JSON.parse(localStorage.getItem("cishu"))
    : [{}]
);

function delLocalStorage() {
  localStorage.removeItem("cishu");
  window.$message.error("好的曾总");
  zengZongShuJu.length = 0;
}

function delItem(index) {
  zengZongShuJu.splice(index, 1);
  window.$message.error("没问题曾总");
  localStorage.setItem("cishu", JSON.stringify(zengZongShuJu));
}


function showModalCard (){
	showModal.value = true
  itemIndex.value = 0
  operate.value = 'add'
	nowTime.value = new dayjs().format("YYYY-MM-DD HH:mm:ss");
  endTime.value = ''
  duration.value = ''
}

function endtimeEdit (id,starttime,textV,index){
	showModal.value = true
  itemIndex.value = index
  operate.value = 'edit'
	nowTime.value = starttime
  textValue.value = textV == '工作巡查' ? '工作巡查' : '摸鱼巡查'
  endTime.value = new dayjs().format("YYYY-MM-DD HH:mm:ss");
  duration.value =  dayjs(endTime.value).diff(nowTime.value,'minute')
}

function enterData (index){
const dataTest = reactive({
	id:'',	
	starttime:'',
	text:'',
	endtime:'',
	timelength :''
})
console.log(index);
showModal.value = false
	if( operate.value == 'add'){
		
		dataTest.id = new Date().getTime()
		dataTest.starttime = nowTime.value
		dataTest.text = textValue.value
		zengZongShuJu.push(dataTest);
		localStorage.setItem("cishu", JSON.stringify(zengZongShuJu));
		window.$message.success("巡查工作成功");
		textValue.value = ''	
	}else{
    dataTest.id = new Date().getTime()
		dataTest.starttime = nowTime.value
		dataTest.text = textValue.value
    dataTest.endtime = endTime.value
    dataTest.timelength = duration.value
    
    zengZongShuJu.splice(itemIndex.value , 1);
    zengZongShuJu.splice(itemIndex.value , 0, dataTest);
		localStorage.setItem("cishu", JSON.stringify(zengZongShuJu));
		window.$message.success("巡查结束");
		textValue.value = ''	
    
	}
	
}



</script>
