<template>
  <n-space style="margin: 10px">
    <n-button type="success" @click="exportTime">曾总巡查工作次数+1</n-button>
    <n-popconfirm @positive-click="delLocalStorage">
      <template #trigger>
        <n-button type="error">我从没来过</n-button>
      </template>
      我曾的没有来过
    </n-popconfirm>
    <n-button type="info" @click="isTime = !isTime">自定义时间</n-button>
    <n-date-picker
      v-model:value="datetime"
      type="datetime"
      :clearable="true"
      v-show="isTime"
      @confirm="confirmTime"
      :is-date-disabled="disablePreviousDate"
    />
  </n-space>
  <n-scrollbar style="max-height: 720px">
    <n-grid :x-gap="12" :y-gap="8" :cols="4">
      <n-grid-item v-for="(item, index) in zengZongShuJu" :key="index">
        <n-card title="曾总到访时间">
          <template #header-extra>
            <n-popconfirm @positive-click="delItem(index)">
              <template #trigger>
                <n-button size="tiny" type="error">这次不算</n-button>
              </template>
              好的曾总
            </n-popconfirm>
          </template>
          <n-text>{{ item }}</n-text>
        </n-card>
      </n-grid-item>
    </n-grid>
  </n-scrollbar>

  <n-h3 class="word-count">
    曾总巡查工作次数共计：<n-text type="error">{{
      zengZongShuJu.length
    }}</n-text
    >次
  </n-h3>
</template>

<script setup>
import { reactive, ref } from "@vue/reactivity";
import dayjs from "dayjs";
import { useMessage } from "naive-ui";
const nowTime1 = ref();
const isTime = ref(false);
window.$message = useMessage();
const datetime = ref();
let zengZongShuJu = reactive(
  JSON.parse(localStorage.getItem("cishu"))
    ? JSON.parse(localStorage.getItem("cishu"))
    : []
);
function exportTime() {
  const nowTime = new dayjs().format("YYYY-MM-DD hh:mm:ss A");
  nowTime1.value = nowTime;
  zengZongShuJu.push(nowTime1.value);
  localStorage.setItem("cishu", JSON.stringify(zengZongShuJu));
  window.$message.success("巡查工作成功");
}

function delLocalStorage() {
  localStorage.removeItem("cishu");
  window.$message.error("好的曾总");
  zengZongShuJu.length = 0;
}

function delItem(index) {
  zengZongShuJu.splice(index, 1);
  window.$message.error("没问题曾总");
  localStorage.setItem("cishu", JSON.stringify(zengZongShuJu));
}

function confirmTime(datetimevalue) {
  let customTime = dayjs(datetimevalue).format("YYYY-MM-DD hh:mm:ss A");
  zengZongShuJu.push(customTime);
  localStorage.setItem("cishu", JSON.stringify(zengZongShuJu));
  isTime.value = false
}

function disablePreviousDate(ts) {
  return ts > Date.now();
}
</script>
<style scoped>
.word-count{
  position: absolute;
  bottom: 0;
  left: 35%;
}
</style>
